.night {
  position: absolute;
  width: 100%;
  height: 90vh;
  &.dark {
    background: linear-gradient(to bottom, #3e3f3f 0%, #0a0c5c 100%);
  }

  &.light {
    background: inherit;
  }
}

.root {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));

  align-items: center;
  gap: 1rem;
  margin: 0 auto;
  height: 100%;
}

.item {
  cursor: pointer;
  transition: transform 0.5s linear;

  &:hover {
    transform: translateY(-5px);
  }
  &-header {
    display: flex;
    align-items: center;
  }
}
