.favorite-action {
  display: flex;
  align-items: center;
  justify-content: center;
}
.weather-data-container {
  border-radius: 15px;
  padding: 1rem;

  &.dark {
    background: rgba(106, 106, 106, 0.234);
  }

  &.light {
    background: rgba(255, 255, 255, 0.645);
  }
}

.current-weather-data-display {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border-radius: 15px;
  margin: 1rem 0;
  padding: 1rem;

  .condition {
    display: flex;
    align-items: center;
  }
}

@media (max-width: 600px) {
  .header {
    flex-direction: column;
    align-items: center;
  }
}
