.card {
  // display: flex;
  // align-items: center;
  // justify-content: center;
  // min-height: 300px;
  // flex-direction: column;
  display: grid;
  grid-template-rows: 50px 250px;
  position: relative;

  .header {
    font-size: 1.2rem;
    font-weight: 500;

    position: relative;
    // top: 0;
    // width: 100%;
    // text-align: center;
    // min-height: 50px;

    &.light {
      background: white;
    }
    &.dark {
      background: rgb(67, 67, 67);
    }

    p {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
    }
  }
  .item {
    border-radius: 15px !important;
    display: flex;
    align-items: center;
    flex-direction: column;
    padding: 0;
  }
}
